/* Mine
================ */
a,
li,
ul,
p {
  margin: 0;
  padding: 0;
  color: inherit;
  list-style: none;
  text-decoration: none;
}
button,
input {
  border: none;
  outline: none;
  background: transparent none repeat 0 0 / auto auto padding-box border-box scroll;
  background: initial;
  color: inherit;
}

* {
  box-sizing: border-box;

  -webkit-tap-highlight-color: transparent;
}

*::-webkit-scrollbar {
  display: none;
}

html,
body {
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  -webkit-text-size-adjust: 100%;

  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body {
  position: relative;
}
* {
  box-sizing: border-box !important;

  font-family: "Barlow", sans-serif;
}

:root {
  --list_h: 68px;
  --list-border: 1px solid rgba(255, 255, 255, 8%);

  --back-1: #363636;

  --white: #eee;
  --wht: #fff;
  --dark-800: #242424;
  --dark-800: #1a1a1a;
  --txt: #999;
  --txt-alt: #808080;
  --txt-alt-2: #aaa;
  --sm-hd-height: 60px;
  --sm-hd-height-half: 60px;
  --sm-nav-height: 70px;
  --main-col: #2958ff;
  --main-col-inv: #ccf381;

  /* --main-col: #31b7a3;
  --main-col-inv: #ce485c; */
  --bord-dark: #666;
  /* --main-col: #0300e2; */
  --l1: #e6e6e635;
}

main {
  width: 100vw;
  min-height: calc(100vh - 70px);
  padding-bottom: 70px;
  padding-bottom: var(--sm-nav-height);
}

.light_theme {
  --color: "#808080";
  --txt-alt: #555;
  --wht: #222;

  --back-1: #e3e3e3;
  --l1: #333;

  --list-border: 1px solid rgba(0, 0, 0, 18%);

  --background: var(--white);
  --border-default: rgba(0, 0, 0, 18%);
  background-color: var(--background);
}
.dark_theme {
  --color: "#808080";
  --background: var(--dark-800);
  --border-default: rgba(255, 255, 255, 8%);
  background-color: var(--background);
}

.big_screen {
  --sm-hd-height: 120px;
}

/* Header Mobile */
.small_header {
  top: 0;
  left: 0;
  z-index: 90;
  width: 100vw;
  display: flex;
  font-size: 13px;
  color: #fff;
  color: var(--wht);
  position: fixed;
  flex-direction: column;
  background-color: var(--background);
  border-bottom: 1px solid var(--border-default);
}

/* Coming Soon
================= */
.notResponsiveWarning {
  width: 100%;
  display: flex;
  font-size: 13px;
  flex-direction: row;
  padding: 0px 6vw;
  height: 60px;
  height: var(--sm-hd-height-half);
  justify-content: center;
  text-transform: uppercase;
  align-items: center;
  color: #fff;
  background: #2958ff;
  white-space: nowrap;
  overflow: hidden;
  border-bottom: 1px solid var(--border-default);
}
.notResponsiveWarning p {
  text-align: center;
}

.small_header_inn {
  width: 100%;
  display: flex;
  font-size: 13px;
  flex-direction: row;
  height: 60px;
  height: var(--sm-hd-height-half);
  justify-content: space-between;
}

.small_header_inn p {
  opacity: 0.8;
}

.light_theme .small_header_inn {
  font-weight: 500;
}

.sm_act_menu_butt {
  height: 60px;
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: center;
  padding-bottom: 2px;
  margin-right: 20px;
}
.react-switch {
  margin: 0px 10px;
  border: 1px solid #fff;
  border: 1px solid var(--wht);
}
.react-switch-handle div {
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
}
/* Navbar Mobile */
.ft_sm {
  bottom: 0;
  width: 100vw;
  z-index: 90;
  height: 70px;
  height: var(--sm-nav-height);
  position: fixed;
  display: flex;
  justify-content: center;
  flex-direction: column;
  background-color: var(--background);
  border-top: 1px solid var(--border-default);
}

.ft_sm_inn {
  color: #fff;
  color: var(--wht);
  height: 100%;
  display: flex;
  width: 90vw;
  flex-direction: row;
  max-width: 350px;
  margin: auto;
  align-items: center;
  justify-content: space-between;
}
.ft_sm_li {
  display: flex;
  font-size: 13px;
  align-items: center;
  flex-direction: column;
}
.ft_sm_li a:active,
.ft_sm_li a:focus {
  border: none;
  outline: none;
}

.ft_sm_li i {
  font-size: 20px;
  margin-bottom: 2px;
}

/* Menu Mobile */
.mn_sm {
  top: 0px;
  width: 100vw;
  height: 100%;
  z-index: 99;
  position: fixed;
  overflow: hidden;

  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex-direction: column;
  font-family: "Barlow", sans-serif;

  background: rgba(0, 0, 0, 0.4);
}
.mn_sm_modal {
  width: 100vw;
  display: flex;
  color: #fff;
  color: var(--wht);
  font-size: 15px;
  font-weight: 500;
  align-items: center;
  box-sizing: border-box;
  flex-direction: column;
  padding-top: 50px;
  padding-bottom: 20px;
  background: #363636;
  background: var(--back-1);
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  border-top: 1px solid var(--border-default);
}
.mn_sm_modal_inn {
  width: 100%;
  max-width: 90vw;
}
.mn_l1 {
  padding-bottom: 10px;
  border-bottom: 1px solid #e6e6e635;
  border-bottom: 1px solid var(--l1);
}
.mn_l1 li {
  margin-bottom: 16px;
}
.mn_l1 li a i {
  margin-right: 15px;
}

.mn_l2 {
  padding-top: 25px;
}
.mn_l2 li a {
  display: flex;
  flex-direction: row;
  margin-bottom: 16px;
}
.mn_l2 li a i {
  margin-right: 15px;
}

.mn_sm_modal .Marquee__Wrapper {
  margin: 0px !important;
  opacity: 0.3;
  margin-top: 4px;
}
.mn_sm_modal .Marquee__TextGroup {
  animation-duration: 60s !important;
}
.mn_sm_modal .Marquee__Word {
  font-size: 14px !important;
}

/* Algo connect */
.algo_connect_hd {
  display: flex;
  font-size: 15px;
  color: #fff;
  color: var(--wht);
  text-transform: uppercase;
  letter-spacing: 1px;
  padding: 13px 0px 16px;
  font-family: "Barlow", sans-serif;

  position: -webkit-sticky;
  position: sticky;
  z-index: 2;
  opacity: 0.7;
  background: var(--background);
  top: calc(60px);
  top: calc(var(--sm-hd-height));
}

.connect_butt {
  display: flex;
  padding: 25px 0px;
  align-items: center;
  flex-direction: row;
  margin: 10px 0px;
  cursor: pointer;
  border-bottom: 1px solid #e6e6e635;
  border-bottom: 1px solid var(--l1);
}
.connect_butt:nth-child(4) {
  margin-bottom: 20px;
  border-bottom: none;
}
.connect_wallet_img {
  width: 20px;
  height: 20px;
  border-radius: 100%;
  overflow: hidden;
  display: flex;
  font-size: 14px;
  align-items: center;
  justify-content: center;
  border: 1px solid #aaa;
}
.connect_wallet_img img {
  width: 20px;
  height: 20px;
  object-fit: cover;
  border-radius: 100%;
}
.connect_wallet_txt {
  font-size: 14px;
  font-weight: 500;
  margin-left: 15px;
}

/* Elections Modal */
.modal_cov {
  top: 0px;
  width: 100vw;
  height: 100vh;
  z-index: 100;
  position: fixed;

  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  font-family: "Barlow", sans-serif;

  background: rgba(0, 0, 0, 0.4);
}
.close_modal {
  width: 420px;
  display: flex;
  max-width: 88vw;
  font-size: 13px;
  font-weight: 600;
  margin-top: 10px;
  color: #2958ff;
  color: var(--main-col);
  border-radius: 8px;
  align-items: center;
  background: #eeeeee;
  flex-direction: column;
  justify-content: center;
  text-transform: uppercase;
  border: 1px solid rgba(255, 255, 255, 8%);
  border: var(--list-border);
  padding: 14px 18px 14px 16px;
}

.modal_cont {
  width: 420px;
  max-width: 88vw;
  min-height: 300px;

  display: flex;
  border-radius: 8px;
  flex-direction: column;
  background: #363636;
  background: var(--back-1);
  background: #eeeeee;
  border: 1px solid rgba(255, 255, 255, 8%);
  border: var(--list-border);
  padding: 12px 18px 12px 16px;
}
.modal_r1 {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.modal_elt_img img {
  width: 30px;
  height: 30px;
  object-fit: cover;
  border-radius: 100%;
  border: 1px solid #ccc;
  border: 1px solid #2958ff;
  border: 1px solid var(--main-col);
}
.modal_elt_tit {
  margin-left: 20px;
  font-weight: 500;
  font-size: 14px;
  /* color: var(--main-col); */
}

.modal_elt_desc {
  margin-top: 10px;
  font-size: 12px;
  line-height: 14px;
}

/*  */
.modal_cand {
  margin-top: 20px;
}
.modal_cand_hd {
  display: flex;
  font-size: 13px;
  opacity: 0.7;
  margin: 13px 0px;
  flex-wrap: wrap;
  text-transform: uppercase;
}

.modal_cand_list {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}
.cand_item {
  display: flex;
  align-items: center;
  flex-direction: row;
  margin-right: 15px;
  margin-bottom: 10px;
}
.cand_img_cont img {
  width: 20px;
  height: 20px;
  object-fit: cover;
  border-radius: 100%;
  border: 1px solid #ccc;
}

.cand_det {
  font-size: 12px;
  font-weight: 500;
  margin-left: 5px;
}
.modal_butts {
  display: flex;
  margin: 30px 0px 20px;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
}
.modal_butts button {
  font-size: 13px;
  color: #fff;
  color: var(--wht);
  padding: 10px 20px;
  background: #000;
  border-radius: 8px;
}

